@mixin hover($_property, $_toValue) {
  transition: #{$_property} 0.3s;
  &:hover {
    #{$_property}: $_toValue;
  }
}

@mixin on-tablet() {
  @media (min-width: $tablet-min-width) {
    @content;
  }
}

@mixin on-desktop() {
  @media (min-width: $desktop-min-width) {
    @content;
  }
}

@mixin content-padding-inline() {
  padding-inline: 20px;

  @include on-tablet {
    padding-inline: 39px;
  }

  @include on-desktop {
    max-width: 1020px;
    margin-inline: auto;
    padding-inline: 55px;
  }
}

@mixin page-grid {
  --columns: 2;
  display: grid;
  column-gap: 20px;
  grid-template-columns: repeat(var(--columns), 1fr);

  @include on-tablet {
    --columns: 6;
    column-gap: 24px;
  }

  @include on-desktop {
    --columns: 12;
  }
}


a {
  text-decoration: none;
}

p,
img,
h1,
h2,
h3,
h4,
body {
  padding: 0;
  margin: 0;
}

